<template>
  <div class="container" style="margin: 15px 15px;">
    <!-- 页面名字 -->
    <el-row type="flex" justify="start" style=" font-size: 16px; color: #303133;">{{pageTitle}}</el-row>
    <el-row type="flex" justify="start" style="margin-top: 30px;">
     <el-form ref="form" :model="form" :rules="rules" label-width="80px" label-position="right">
        <el-form-item label="资讯图片">
          <el-upload class="avatar-uploader"
                    action="https://jsonplaceholder.typicode.com/posts/"
                    :show-file-list="false"
                    :on-success="handleAvatarSuccess"
                    :before-upload="beforeAvatarUpload">
            <img v-if="imageUrl" :src="form.imageUrl" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
            <i class="avatar-limit">图片小于1M,格式jpg,png,jpeg</i>
        </el-form-item>
        <el-form-item label="资讯类别">
          <el-radio-group v-model="form.radio">
            <el-radio :label="0">不链接</el-radio>
            <el-radio :label="1">链接公众号文章</el-radio>
            <el-radio :label="2">自定义页面</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item v-if="form.radio == 1" label="文章地址">
          <el-input v-model="form.name" placeholder="请输入公众号文章地址"></el-input>
        </el-form-item>
        <el-form-item v-if="form.radio == 2" label="链接页面">
          <el-select v-model="form.region" filterable placeholder="请选择链接页面">
            <el-option label="区域一" value="shanghai"></el-option>
          </el-select>
        </el-form-item>
        <el-button type="primary" @click="submitForm('form')" style="">保存</el-button>
      </el-form>
    </el-row>
  </div>
</template>
<script>

export default {
  components: {
  },

  data () {
    return {
      pageState: '', // 1新增，2编辑，3预览
      pageTitle: '', // 页面标题
      form: {
        name: '',
        region: '',
        date1: '',
        date2: '',
        baseNum: 0,
        delivery: false,
        type: [],
        resource: '',
        desc: '',
        radio: 0,
        imageUrl: ''
      },
      rules: { // 校验规则
        name: { required: true, message: '请输入资讯标题名称', trigger: 'blur' }
      }
    }
  },

  watch: {
    pageState (newValue) {
      switch (newValue) {
        case 1:
          this.pageTitle = '新增轮播图'
          break
        case 2:
          this.pageTitle = '编辑轮播图'
          break
        case 3:
          this.pageTitle = '轮播图详情'
          break
      }
    }
  },

  methods: {
    // 提交表单
    submitForm (formName) {
      console.log('in')
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('submit!')
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  },

  mounted () {
    this.pageState = this.$route.query.pageState || 1
  }
}
</script>

<style>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  .avatar-limit{
    font-size: 12px;
  }
</style>
